@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Racing+Sans+One&family=Covered+By+Your+Grace&display=swap');

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: #CE0018;
  font-family: 'Inter', sans-serif;
  position: relative; /* Ensure positioning context for the dropdown */
}

.navbar-logo {
  display: flex;
  align-items: center;
  margin-right: 0.5rem;
}

.navbar-logo a {
  color: white;
  text-decoration: none;
  font-size: 2.5rem;
  font-family: 'Covered By Your Grace', cursive;
  font-weight: bolder;
}

.navbar-toggle {
  display: none;
  font-size: 2rem;
  font-weight: bold;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
  font-weight: bold;
  flex: 1;
  justify-content: flex-start; /* Keep links on the left for desktop */
}

.navbar-links li {
  display: inline;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  font-size: 1.35rem;
  font-family: 'Inter', sans-serif;
}

.navbar-links a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: flex-end;
    align-items: flex-end;
    padding: 1rem;
    position: relative; /* Ensures the dropdown is positioned relative to the navbar */
  }

  .navbar-logo {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar-toggle {
    display: block;
  }

  .navbar-links {
    display: none;;
    flex-direction: column;
    width: 100%;
    position: absolute; /* Positioning the dropdown */
    top: 60%; /* Positioning just below the navbar */
    right: 0;
    background: #CE0018;
    padding: 1rem;
    border-radius: 0.5rem;
  }

  .navbar-links.open {
    display: flex;
  }

  .navbar-links li {
    width: 100%;
    text-align: center;
  }

  .navbar-links a {
    padding: 1rem;
    width: 100%;
    display: block;
  }
}