.home-container {
  padding: 2rem;
  padding-left: 4rem; /* Increase left padding to shift content to the right */
}

.home-container p {
  font-size: 1.2rem;
  line-height: 1.7;
}

.home-container h1 {
  font-family: 'Inter', sans-serif;
}

.whoami-section {
  margin-bottom: 2rem; /* Increase spacing between sections */
}

.whoami-section h2 {
  font-family: 'Fira Code', monospace; 
}

.skills-section {
  font-family: 'Fira Code', monospace;
  margin-top: 2rem; /* Increase spacing between sections */
  margin-bottom: 2rem;
}

.prog-languages,
.libraries-and-frameworks,
.other-technologies {
  display: flex;
  flex-wrap: wrap; /* Allow wrapping to next line if too many icons */
  gap: 2rem; /* Adjust the spacing between icons as needed */
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.prog-languages img,
.libraries-and-frameworks img,
.other-technologies img {
  width: 100px; /* Adjust the size of the icons as needed */
  height: 100px; /* Adjust the size of the icons as needed */
  flex-shrink: 0; /* Prevent icons from shrinking */
}

.libraries-and-frameworks .icon-wrapper,
.other-technologies .icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white; /* White background */
  border-radius: 50%; /* Circle shape */
  width: 100px; /* Adjust size to ensure the circle is visible around the icon */
  height: 100px; /* Adjust size to ensure the circle is visible around the icon */
  padding: 10px; /* Optional padding to make the icon smaller inside the circle */
  box-sizing: border-box; /* Ensure padding is included in the element's total size */
}

.libraries-and-frameworks .icon-wrapper img,
.other-technologies .icon-wrapper img {
  width: 80px; /* Adjust size of the inner icon */
  height: 80px; /* Adjust size of the inner icon */
}

@media (max-width: 768px) {
  .home-container {
    padding: 1rem;
  }
  
  .home-container h1 {
    font-size: 1.5rem;
  }
  
  .home-container p {
    font-size: 1rem;
    line-height: 1.5;
  }

  .prog-languages,
  .libraries-and-frameworks,
  .other-technologies {
    justify-content: flex-start; /* Start from left on smaller screens */
  }

  .prog-languages img,
  .libraries-and-frameworks img,
  .other-technologies img {
    width: 30px; /* Adjust the size of the icons for smaller screens */
    height: 30px;
  }

  .libraries-and-frameworks .icon-wrapper,
  .other-technologies .icon-wrapper {
    width: 30px;
    height: 30px;
  }

  .libraries-and-frameworks .icon-wrapper img,
  .other-technologies .icon-wrapper img {
    width: 24px;
    height: 24px;
  }
}

@media (max-width: 480px) {
  .home-container {
    padding: 0.5rem;
  }
  
  .home-container h1 {
    font-size: 1.2rem;
  }
  
  .home-container p {
    font-size: 0.9rem;
    line-height: 1.4;
  }

  .prog-languages,
  .libraries-and-frameworks,
  .other-technologies {
    justify-content: flex-start; /* Start from left on very small screens */
  }

  .prog-languages img,
  .libraries-and-frameworks img,
  .other-technologies img {
    width: 40px; /* Adjust the size of the icons for very small screens */
    height: 40px;
  }

  .libraries-and-frameworks .icon-wrapper,
  .other-technologies .icon-wrapper {
    width: 40px;
    height: 40px;
  }

  .libraries-and-frameworks .icon-wrapper img,
  .other-technologies .icon-wrapper img {
    width: 30px;
    height: 30px;
  }
}

.contact-icons {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
}

.contact-icons img {
  width: 40px; /* Adjust the size of the icons as needed */
  height: 40px; /* Adjust the size of the icons as needed */
}